.CreateTodoButton__add {
    display:block;
    width: 50px;
    height: 50px;
    margin: 0 auto;
    border-radius: 50%;
    border: none;
    background-color: #070231;
    color: #fff;
    font-size: 2rem;
    z-index: 2;
}

.CreateTodoButton__add:active {
    transform: scale(0.95);
}